let etempEmail_ID = 'template_9fjmuo5';
let eService_ID = 'service_mev14lc';

let HOST_MAIL = "http://localhost:3002";
// Khởi tạo ngay trong module

export function emailJS_sendMail_thongBao_DON_HANG_MOI(params) {

    // Các key trong params phải TRÙNG KHỚP với {{biến}} trong Template đã tạo
    const paramsTEST = {
        param_to_email: "lqvinh.hsu@gmail.com",
        param_message: params.HTML_Table_listProduct,
        param_customer_name: params.phone,
    };

    emailjs.init(eYOUR_PUBLIC_KEY); 
    emailjs.send(eService_ID, etempEmail_ID, paramsTEST)
    .then(() => {
        showToast("Đã đặt hàng thành công. Cảm ơn bạn đã mua hàng!", "success");
    })
    .catch(() => {
        showToast("Gửi mail thất bại!", "error");
    });
}


export function email_sendMail_thongBao_DON_HANG_MOI(params, cb) {
  const data = {
    to: "lqvinh.hsu@gmail.com",
    subject: `[MUAVUI] Đơn hàng mới từ KH [${params.phone}]`,
    htmlContent: params.HTML_Table_listProduct,
  };

  fetch(`${HOST_MAIL}/send-email`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(data),
  })
    .then((response) => {
      if (response.ok) {
        if (cb) {
          cb(true, 'email thanh cong');
        }
      } else {
        if (cb) {
          cb(false, 'email that bai');
        }
      }
    })
    .catch((err) => {
       if (cb) {
          cb(false, 'catch email that bai');
      }
      console.error(err);
    });
}


window.emailJS_sendMail_thongBao_DON_HANG_MOI = emailJS_sendMail_thongBao_DON_HANG_MOI;
window.email_sendMail_thongBao_DON_HANG_MOI = email_sendMail_thongBao_DON_HANG_MOI;

